import { STRATEGY_MESSAGES } from '../strategyMessages';

export class galateaMessageService {
    static newMessage(message){
        var json=JSON.parse(message.data);
        if(STRATEGY_MESSAGES[json.action]){
            STRATEGY_MESSAGES[json.action](json);
        }
    }
}


