export const STRATEGY_DISPOSITION = {
    "onDispositions": function (json){
      onDispositions(json.data);
    },
    "onDisposeApplied": function(){
      onDisposeApplied();
    },
    "onPhoneNumbers": function(json){
      onPhoneNumbers(json.data);
    },
    "onReprogramCall": function(json){
      onReprogramCall(json.data);
    },
    "onReprogramSuccess": function(){
      onReprogramSuccess();
    },
    "onDispositionsAndNumbers": function(json){
      onDispositionsAndNumbers(json.data);
    },
    "errorOnDispose":function(json){
      errorOnDispose(json.data)
    }
}



