import { SingletonWebSocket } from '../singletonWebSocket'

export class IPHelper{  
    constructor()
    {
        this.ip = 0;
    }
    GetIp() {
        if ("ActiveXObject" in window) {
            try {
              this.getIEIPAddress();
            } catch (err) {
            }
           }
        else {
            this.getIPAddress();
        }
    }
    getIEIPAddress(){
        var locator = new window.ActiveXObject ("WbemScripting.SWbemLocator");
        var service = locator.ConnectServer(".");
        var properties = service.ExecQuery("SELECT * FROM Win32_NetworkAdapterConfiguration");
        var e = new window.Enumerator (properties);
        for (;!e.atEnd();e.moveNext ())
        {
          var p = e.item ();
          if (p.IPAddress!=null ) {
            SingletonWebSocket.setIPAddress(p.IPAddress(0));
              break;
          }
        }
      }
    
       getIPAddress() { 
        window.RTCPeerConnection = window.RTCPeerConnection || window.mozRTCPeerConnection || window.webkitRTCPeerConnection;   //compatibility for firefox and chrome
        if(window.RTCPeerConnection){
          var pc = new RTCPeerConnection({iceServers:[]}), noop = function(){};      
          pc.createDataChannel("");    //create a bogus data channel
          var _this = this;
          pc.createOffer(pc.setLocalDescription.bind(pc), noop);    // create offer and set local description
          pc.onicecandidate = function(ice){  //listen for candidate events
              if(!ice || !ice.candidate || !ice.candidate.candidate)  return;
              var ip = /([0-9]{1,3}(\.[0-9]{1,3}){3}|[a-f0-9]{1,4}(:[a-f0-9]{1,4}){7})/.exec(ice.candidate.candidate)[1];
              _this.setIP(ip);
              pc.onicecandidate = noop;
          };
        }
        else{
            this.setIP("")
        }
      }

      setIP(ipAddress){
        SingletonWebSocket.setIPAddress(ipAddress);
      }
     

}


